/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

.text {
  color: #a3a5ae;
  font-size: 1.6rem;
  font-family: Poppins, sans-serif;
  line-height: 160%;
  letter-spacing: -0.025em;
}
.text-h2 {
  color: #4c4e61;
  font-weight: 200;
  font-size: 2.4rem;
  line-height: 150%;
  letter-spacing: 0.01em;
}
.text-h3 {
  color: #4c4e61;
  font-size: 2rem;
  line-height: 135%;
  letter-spacing: 0;
}
.text-strong {
  font-weight: 600;
}
.text-sm {
  font-size: 1.3rem;
  line-height: 180%;
  letter-spacing: 0.01em;
}

.feature {
  padding: 8.4rem 3.2rem 8rem;
  background: #fafafa;
}
.feature_header {
  max-width: 52rem;
  margin: auto;
  text-align: center;
}
.feature_heading {
  margin: 0;
}
.feature_caption {
  margin: 1.6rem 0 0;
}
.feature_list {
  display: grid;
  grid-gap: 2.4rem;
  justify-content: center;
  margin-top: 7.5rem;
  padding: 0;
  list-style: none;
}

.card {
  position: relative;
  display: flex;
  flex-flow: column;
  box-sizing: border-box;
  height: 100%;
  padding: 2.8rem;
  overflow: hidden;
  background: white;
  border-radius: 0.8rem;
  box-shadow: 0 1.6rem 1.6rem rgba(84, 158, 242, 0.1);
}
.card::before {
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  height: 0.4rem;
  transform: translateX(-50%);
  content: "";
}
.card_heading {
  margin: 0;
  font-weight: 600;
}
.card_text {
  flex: 1;
  margin: 0.4rem 0 0;
}
.card_icon {
  align-self: flex-end;
  width: 5.7rem;
  height: 5.7rem;
  margin-top: 3.2rem;
}
.card:nth-child(1)::before {
  background: #45d3d3;
}
.card:nth-child(1) > .card_icon {
  background: url(icon-supervisor.svg) center;
  background-size: contain;
}
.card:nth-child(2)::before {
  background: #ea5353;
}
.card:nth-child(2) > .card_icon {
  background: url(icon-team-builder.svg) center;
  background-size: contain;
}
.card:nth-child(3)::before {
  background: #fcaf4a;
}
.card:nth-child(3) > .card_icon {
  background: url(icon-karma.svg) center;
  background-size: contain;
}
.card:nth-child(4)::before {
  background: #549ef2;
}
.card:nth-child(4) > .card_icon {
  background: url(icon-calculator.svg) center;
  background-size: contain;
}

@media (min-width: 48rem) {
  .text-h2 {
    font-size: 3rem;
  }

  .feature_list {
    grid-template-columns: repeat(2, 32rem);
    margin: 7rem auto 0;
  }
}
@media (min-width: 75rem) {
  .text-h2 {
    font-size: 3.6rem;
    line-height: 142.5%;
  }

  .feature {
    padding-top: 8rem;
  }
  .feature_list {
    grid-gap: 3rem;
    grid-template: repeat(4, 1fr)/repeat(3, 35rem);
    margin: 6.4rem auto 0;
  }

  .card {
    padding: 3.2rem;
  }
  .card:nth-child(1) {
    grid-area: 2/1/span 2;
  }
  .card:nth-child(2) {
    grid-area: 1/2/span 2;
  }
  .card:nth-child(3) {
    grid-area: 3/2/span 2;
  }
  .card:nth-child(4) {
    grid-area: 2/3/span 2;
  }
  .card_text {
    margin-top: 1rem;
  }
  .card_icon {
    width: 6.4rem;
    height: 6.4rem;
    margin-top: 3.9rem;
  }
}